body {
  font-family: $font-base;
  &.fixed {
    height: 100vh;
    overflow: hidden;
    display: flex;
  }
}


// =========================================
// CONTROL VIEW ELEMENT
// =========================================

.pc-only {
  display: block;
  @include mq() {
		display: none !important;
	}
}

.pc-only2 {
  display: inline-block;
  @include mq() {
	  display: none !important;
	}
}

.sp-only {
  display: none;
  @include mq() {
	  display: block !important;
	}
}


// =========================================
// WRAPPER
// =========================================
.wrapper {
  // min-width: 1400px;
